.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  position: relative;
  height: 40vmin;
  pointer-events: none;
  border-radius: 90%;
  padding: 0;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.profileButton {
  justify-content: center;
  justify-items: center;
  background-color: #282c34;
  border: none;
  color: white;
  padding: 2px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  border-radius: 60%;
  z-index: 0;
}

.buttonDiv {
  justify-content: center;
  justify-items: center;
  background-color: #282c34;
  border: none;
  color: white;
  padding: 2px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  border-radius: 60%;
  z-index: 0;
}

.loading-button__confetti {
  position: absolute !important;
  top: 50%;
  left: 50%;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
